import React, { useEffect, useState } from 'react';
import Api from "../api/api";

const Modal = (props) => {
    const [eicData, setEicData] = useState([]);
    const [deliverablesData, setDeliverablesData] = useState([]);
    const [ensData, setEnsData] = useState([]);
    const [sourceData, setSourceData] = useState([]);
    const [domainData, setDomainData] = useState([]);
    const [finalObj, setFinalObj] = useState({
        "hdl": "",
        "status": "PENDING",
        "params": {
            "eic_handle": "",
            "manifest_name": "",
            "inputfile": "",
            "worksheets": "",
            "object_type": "",
            "deliverable": "",
            "source_handle": "",
            "classification": "",
            "ens_name": "",
            "terminate_attributes": "ignore"
        }
    }
    );
    const selectClass = "mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm";
    const lableClass = "block text-sm font-medium text-gray-700";
    let userName = localStorage.getItem("userName");
    let password = localStorage.getItem("password");
    let token = localStorage.getItem("b_token");
    let prefixUrl = localStorage.getItem("prefixUrl");
    let clsToken = localStorage.getItem("b_token_cls");
    let baseUrl = `https://${prefixUrl}.pim360.io/api`;
    let cls_baseUrl = `https://${prefixUrl}.cls360.io/api`;
    const objectType = [
        { value: "TAGGED_ITEM", name: "Tagged Item" },
        { value: "EQUIPMENT_ITEM", name: "Equipment Item" },
        { value: "EQUIPMENT_MODEL", name: "Equipment Model" },
        { value: "DOCUMENT", name: "Document" }
    ];
    const classification = [
        { value: "cls", name: "CLASS" },
        { value: "ens", name: "ENS" }
    ];

    useEffect(() => {
        getEICList();
    }, []);

    const onClickCancel = () => {
        if (props.onClickCancel) {
            props.onClickCancel();
        };
    };

    const getEICList = () => {
        console.log('inside getEICList ---------->')
        let url = `${baseUrl}/eic/list`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                let eic_List = responseData.rows.map((item) => {
                    let keyToGetValue = Object.keys(item)[4];
                    if (item[keyToGetValue] != undefined) {
                        return { "name": item[keyToGetValue], "value": item.id }
                    }
                    else {
                        return null
                    }
                });
                let filteredList = eic_List.filter(item => item != null);
                // console.log('filteredList -------', JSON.stringify(filteredList))
                setEicData(filteredList)
            }
        }).catch(error => {
            console.log("getEICList error -------->" + error);
        });
    };

    const getDeliverables = (eic) => {
        console.log('inside getDeliverables ---------->');
        let url = `${baseUrl}/eic/${eic}/deliverables`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                // console.log('getDeliverables   -------->', JSON.stringify(responseData));
                let deliverable_keys = Object.keys(responseData.Deliverables)
                if (deliverable_keys.length != null && deliverable_keys.length > 0) {
                    let filter_list = deliverable_keys.filter(e => e != "length");
                    let deliverables_list = [];
                    filter_list.map((filter) => {
                        responseData.Deliverables[filter].map(({ ID, Hdl }) => {
                            deliverables_list.push({ "name": ID, "value": Hdl });
                        });
                    });
                    setDeliverablesData(deliverables_list);
                }
            }
        }).catch(error => {
            console.log("getDeliverables error -------->" + error);
        });
    };

    const getTimeLine = () => {
        console.log('inside getTimeLine ---------->');
        let url = `${baseUrl}/timeline?categories=IMPORT&offset=0&limit=200`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                let tempObj = { ...finalObj };
                tempObj.hdl = responseData[0].Hdl;
                setFinalObj(tempObj);
                onClickCancel();
                console.log('final obj --------', JSON.stringify(tempObj));
            }
        }).catch(error => {
            console.log("getTimeLine error -------->" + error);
        });
    };

    const authCls = () => {
        console.log('inside authCls -------------> ')
        Api.getClsToken(userName, password, prefixUrl).then((response) => {
            if (response.data.access_token != "") {
                //set token in local storage
                localStorage.setItem("b_token_cls", response.data.token_type + " " + response.data.access_token);
                clsToken = localStorage.getItem("b_token_cls");
                getDomains();
            };
        }).catch((e) => {
            //setting common error
            console.log('authCls error-------> ');
        });
    };

    const getDomains = () => {
        console.log('inside getDomains ---------->');
        let url = `${cls_baseUrl}/domains?pageSize=200&page=0`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: clsToken,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                let domain_List = responseData.value.map(({ Name, Hdl }) => {
                    return { "name": Name, "value": Hdl };
                })
                setDomainData(domain_List);
            }
        }).catch(error => {
            console.log("getDomains error -------->" + error);
        });
    };

    const getSourceNames = (hdl) => {
        console.log('inside getSourceNames ---------->');
        let url = `${cls_baseUrl}/domains/${hdl}/classes?filter=%5BObject%20Type%5D%20cnt%20Source&pageSize=200&page=0`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: clsToken,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                let source_List = responseData.value.map(({ Name, Hdl }) => {
                    return { "name": Name, "value": Hdl }
                })
                setSourceData(source_List);
            }
        }).catch(error => {
            console.log("getSourceNames error -------->" + error);
        });
    };

    const getEnsNames = (hdl) => {
        console.log('inside getEnsNames ---------->');
        let url = `${cls_baseUrl}/domains/${hdl}/classes?filter=%5BObject%20Type%5D%20cnt%20Numbering&pageSize=200&page=0`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: clsToken,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                let ens_List = responseData.value.map(({ Name }) => {
                    return { "name": Name, "value": Name }
                })
                setEnsData(ens_List);
            }
        }).catch(error => {
            console.log("getEnsNames error -------->" + error);
        });
    };

    return (
        <div>
            <div style={{ display: "none" }} className="modal-container">
                <div className="modal">
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            Object Type
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                let tempObj = { ...finalObj };
                                tempObj.params.object_type = e.target.value
                                setFinalObj(tempObj);
                            }}
                        >
                            <option value="">Select Object Type</option>
                            {objectType.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            Classification
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                let tempObj = { ...finalObj };
                                tempObj.params.classification = e.target.value;
                                setFinalObj(tempObj);
                            }}
                        >
                            <option value="">Select Classification</option>
                            {classification.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            EIC
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                let tempObj = { ...finalObj };
                                tempObj.params.eic_handle = e.target.value;
                                setFinalObj(tempObj);
                                getDeliverables(e.target.value)
                            }}
                        >
                            <option value="">Select Eic</option>
                            {eicData.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            Deliverables
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                let tempObj = { ...finalObj };
                                tempObj.params.deliverable = e.target.value;
                                setFinalObj(tempObj);
                                authCls();
                            }}
                        >
                            <option value="">Select Deliverables</option>
                            {deliverablesData.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            Domains
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                getSourceNames(e.target.value);
                                getEnsNames(e.target.value);
                            }}
                        >
                            <option value="">Select Deliverables</option>
                            {domainData.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            Source Name
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                let tempObj = { ...finalObj };
                                tempObj.params.source_handle = e.target.value;
                                setFinalObj(tempObj);
                            }}
                        >
                            <option value="">Select Source</option>
                            {sourceData.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <div className="mt-3">
                        <label className="label inline-block mr-3 w-1/4">
                            ENS Name
                        </label>
                        <select className="select inline-block w-2/4"
                            onChange={(e) => {
                                let tempObj = { ...finalObj };
                                tempObj.params.ens_name = e.target.value;
                                setFinalObj(tempObj);
                            }}
                        >
                            <option value="">Select Deliverables</option>
                            {ensData.map((eic) => {
                                return <option value={eic.value}>{eic.name}</option>
                            })}
                        </select>
                    </div>
                    <button className="x-icon" onClick={() => onClickCancel()}>
                        X
                    </button>
                    <div>
                        <button className="mr-3" onClick={() => onClickCancel()}>
                            Cancel
                    </button>
                        <button className="" onClick={() => getTimeLine()}>
                            Submit
                    </button>
                    </div>
                </div>
            </div>
            <div class="fixed z-10 inset-0 overflow-y-auto">
                <div class="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
                    <div class="fixed inset-0 transition-opacity" aria-hidden="true">
                        <div class="absolute inset-0 bg-gray-500 opacity-75"></div>
                    </div>
                    <span class="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
                    <div class="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full" role="dialog" aria-modal="true" aria-labelledby="modal-headline">
                        <div class="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                            <div className="mt-3">
                                <label for="objectType" className={lableClass}>Object Type</label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        let tempObj = { ...finalObj };
                                        tempObj.params.object_type = e.target.value
                                        setFinalObj(tempObj);
                                    }}
                                >
                                    <option value="">Select Object Type</option>
                                    {objectType.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>
                            <div className="mt-3">
                                <label className={lableClass}>
                                    Classification
                                </label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        let tempObj = { ...finalObj };
                                        tempObj.params.classification = e.target.value;
                                        setFinalObj(tempObj);
                                    }}
                                >
                                    <option value="">Select Classification</option>
                                    {classification.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>
                            <div className="mt-3">
                                <label className={lableClass}>
                                    EIC
                        </label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        let tempObj = { ...finalObj };
                                        tempObj.params.eic_handle = e.target.value;
                                        setFinalObj(tempObj);
                                        getDeliverables(e.target.value)
                                    }}
                                >
                                    <option value="">Select Eic</option>
                                    {eicData.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>
                            <div className="mt-3">
                                <label className={lableClass}>
                                    Deliverables
                        </label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        let tempObj = { ...finalObj };
                                        tempObj.params.deliverable = e.target.value;
                                        setFinalObj(tempObj);
                                        authCls();
                                    }}
                                >
                                    <option value="">Select Deliverables</option>
                                    {deliverablesData.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>
                            <div className="mt-3">
                                <label className={lableClass}>
                                    Domains
                        </label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        getSourceNames(e.target.value);
                                        getEnsNames(e.target.value);
                                    }}
                                >
                                    <option value="">Select Deliverables</option>
                                    {domainData.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>
                            <div className="mt-3">
                                <label className={lableClass}>
                                    Source Name
                        </label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        let tempObj = { ...finalObj };
                                        tempObj.params.source_handle = e.target.value;
                                        setFinalObj(tempObj);
                                    }}
                                >
                                    <option value="">Select Source</option>
                                    {sourceData.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>
                            <div className="mt-3">
                                <label className={lableClass}>
                                    ENS Name
                        </label>
                                <select className={selectClass}
                                    onChange={(e) => {
                                        let tempObj = { ...finalObj };
                                        tempObj.params.ens_name = e.target.value;
                                        setFinalObj(tempObj);
                                    }}
                                >
                                    <option value="">Select Deliverables</option>
                                    {ensData.map((eic) => {
                                        return <option value={eic.value}>{eic.name}</option>
                                    })}
                                </select>
                            </div>

                        </div>
                        <div class="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                            <button onClick={() => getTimeLine()} type="button" class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-purple-900 text-base font-medium text-white hover:bg-purple-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm">
                                Submit
                            </button>
                            <button onClick={() => onClickCancel()} type="button" class="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm">
                                Cancel
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Modal;